<template>
  <div class="container">
    <div class="panel panel-default">
      <div class="panel-body">
        <vue-form-generator :schema="schema" :model="model" :options="formOptions"></vue-form-generator>
      </div>
    </div>
    <div v-if="submitInfo" class="panel">
      <div class="panel-heading">{{ submitInfo.data }}</div>
    </div>
  </div>
</template>

<script>
import VueFormGenerator from "vue-form-generator/dist/vfg-core.js";
import "vue-form-generator/dist/vfg-core.css"; // optional core css additions

function updateConnectionString(value) {
  if (value.toLowerCase() === "postgresql") {
    this.model.rdbmsType = "PostgreSQL";
    this.model.port = "5432";
  } else if (value.toLowerCase() === "mysql") {
    this.model.rdbmsType = "MYSQL";
    this.model.port = "3306";
  } else if (value.toLowerCase() === "h2") {
    this.model.rdbmsType = "H2";
    this.model.port = "";
  }

  this.model.connectionString =
    "jdbc:" +
    this.model.rdbmsType.toLowerCase() +
    "://" +
    this.model.hostName +
    (this.model.port !== "" ? ":" + this.model.port : "") +
    "/" +
    this.model.databaseName +
    "?useSSL=false";
}

export default {
  name: "DatabaseConfiguration",
  components: {
    "vue-form-generator": VueFormGenerator.component
  },
  methods: {
    submitFunction: function() {
      var vm = this;
      vm.submitInfo = null;
      this.axios
        .post(
          "/services/elexis-connector/connection",
          this.model
        )
        .then(function(response) {
          vm.submitInfo = response;
          location.reload();
        })
        .catch(function(error) {
          console.log(error);
          vm.submitInfo = response;
        });
    }
  },
  watch: {
    "model.rdbmsType": updateConnectionString,
    "model.hostName": updateConnectionString,
    "model.port": updateConnectionString,
    "model.databaseName": updateConnectionString
  },
  data() {
    return {
      submitInfo: null,
      model: {
        rdbmsType: "MySQL",
        hostName: "localhost",
        port: "3306",
        databaseName: "elexisdb",
        username: "",
        password: "",
        connectionString: "jdbc:mysql://localhost:3306/elexisdb?useSSL=false"
      },
      schema: {
        fields: [
          {
            type: "select",
            label: "Database Management System",
            model: "rdbmsType",
            required: true,
            values: function() {
              return [
                { id: "MySQL", name: "MySQL" },
                { id: "PostgreSQL", name: "PostgreSQL" },
                { id: "H2", name: "H2" }
              ];
            }
          },
          {
            type: "input",
            inputType: "text",
            label: "Hostname",
            model: "hostName",
            required: true
          },
          {
            type: "input",
            inputType: "text",
            label: "Port",
            model: "port"
          },
          {
            type: "input",
            inputType: "text",
            label: "Database name",
            model: "databaseName",
            required: true
          },
          {
            type: "input",
            inputType: "text",
            label: "Username",
            model: "username",
            required: true
          },
          {
            type: "input",
            inputType: "password",
            label: "Password",
            model: "password"
          },
          {
            type: "input",
            inputType: "text",
            label: "Connection string",
            model: "connectionString"
          },
          {
            type: "submit",
            buttonText: "Submit",
            onSubmit: this.submitFunction
          }
        ]
      },
      formOptions: {
        validateAfterLoad: true,
        validateAfterChanged: true
      }
    };
  }
};
</script>

<style>
html {
  font-family: Tahoma;
  font-size: 14px;
}

body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 1.42857143;
  color: #333;
}

pre {
  overflow: auto;
}
pre .string {
  color: #885800;
}
pre .number {
  color: blue;
}
pre .boolean {
  color: magenta;
}
pre .null {
  color: red;
}
pre .key {
  color: green;
}

h1 {
  text-align: center;
  font-size: 36px;
  margin-top: 20px;
  margin-bottom: 10px;
  font-weight: 500;
}

fieldset {
  border: 0;
}

.panel {
  margin-bottom: 20px;
  background-color: #fff;
  border: 1px solid transparent;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
  border-color: #ddd;
}

.panel-heading {
  color: #333;
  background-color: #f5f5f5;
  border-color: #ddd;

  padding: 10px 15px;
  border-bottom: 1px solid transparent;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.panel-body {
  padding: 15px;
}

.field-checklist .wrapper {
  width: 100%;
}
</style>



