<template>
  <div id="app">
    <center><img src='./assets/elexis.png'></center>
    <h1>Elexis-Server Database Connection Configuration</h1>
    <div v-if="dbStatus === 204">
      <DatabaseConfiguration/>
    </div>
    <div v-else-if="dbStatus === 401">
      <h3>Database already configured, re-configuration currently not supported</h3>
    </div>
    <div v-else>
      <h3>Unsupported status: {{ dbStatus }} </h3>
    </div>
  </div>
</template>

<script>
import DatabaseConfiguration from "./components/DatabaseConfiguration";

export default {
  name: "App",
  asyncComputed: {
    dbStatus() {
      this.axios.interceptors.response.use(
        response => {
          return response;
        },
        error => {
          return error.response;
        }
      );

      return this.axios
        .get("/services/elexis-connector/connection")
        .then(function(response) {
          return response.status;
        })
    }
  },
  components: {
    DatabaseConfiguration
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>



